.enter {
  opacity: 0;
  transform: translateX(20%);
}
.enterActive {
  opacity: 1;
  transform: translateX(0);
  transition: all 250ms ease-in-out;
}
.exit {
  opacity: 1;
  transform: translateX(0);
}
.exitActive {
  opacity: 0;
  transform: translateX(20%);
  transition: all 250ms ease-in-out;
}
