@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css);
body {
  font-family: 'Montserrat', sans-serif;
  padding: 20px 0;
  font-size: 18px;
  background-color: #353b48;
}
#root {
  text-align: center;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button {
  font-family: 'Montserrat', sans-serif;
}



button {
  display: block;
  margin: 0 auto;
  line-height: 28px;
  padding: 0 20px;
  background: #ffa580;
  letter-spacing: 2px;
  transition: 0.2s all ease-in-out;
  outline: none;
  border: 1px solid rgba(0, 0, 0, 1);
  box-shadow: 3px 3px 1px #95a4ff,
    3px 3px 1px 1px rgba(0, 0, 0, 1);
  cursor: pointer;
}
button:hover {
  background: rgba(0, 0, 0, 1);
  color: white;
  border: 1px solid rgba(0, 0, 0, 1);
}

button::selection {
  background: #ffc8ff;
}

input {
  display: block;
  width: 100%;
  font-size: 16px;
  line-height: 16px;
  font-family: font-face;
  margin-bottom: 16px;
  border: none;
  border-bottom: 5px solid rgba(0, 0, 0, 1);
  background: #f8f4e5;
  min-width: 250px;
  padding-left: 5px;
  outline: none;
  color: rgba(0, 0, 0, 1);
}
